.drop-photo, .drop-photo * {
  box-sizing: border-box; }

.drop-photo {
  min-height: 150px;
  border: 2px solid rgba(0, 0, 0, 0.3);
  background: white;
  padding: 20px 20px; }

.drop-photo.dz-clickable {
  cursor: pointer; }

.drop-photo.dz-clickable * {
  cursor: default; }

.drop-photo.dz-clickable .dz-message, .drop-photo.dz-clickable .dz-message * {
  cursor: pointer; }

.drop-photo.dz-started .dz-message {
  display: none; }

.drop-photo.dz-drag-hover {
  border-style: solid; }

.drop-photo.dz-drag-hover .dz-message {
  opacity: .5; }

.drop-photo .dz-message {
  text-align: center;
  margin: 2em 0; }

.drop-photo .dz-preview {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin: 16px;
  min-height: 100px; }

.drop-photo .dz-preview:hover {
  z-index: 1000; }

.drop-photo .dz-preview:hover .dz-details {
  opacity: 1; }

.drop-photo .dz-preview.dz-file-preview .dz-image {
  border-radius: 20px;
  background: #999;
  background: linear-gradient(to bottom, #eee, #ddd); }

.drop-photo .dz-preview.dz-file-preview .dz-details {
  opacity: 1; }

.drop-photo .dz-preview.dz-image-preview {
  background: white; }

.drop-photo .dz-preview.dz-image-preview .dz-details {
  -webkit-transition: opacity 0.2s linear;
  -moz-transition: opacity 0.2s linear;
  -ms-transition: opacity 0.2s linear;
  -o-transition: opacity 0.2s linear;
  transition: opacity 0.2s linear; }

.drop-photo .dz-preview .dz-remove {
  font-size: 14px;
  text-align: center;
  display: block;
